class CenterArea extends Component {
    constructor(props){
        super(props)
        this.SetTab = this.SetTab.bind(this)
        this.state = {activeTab: "About"}
    }

    SetTab(tabName){
        this.setState({activeTab: tabName})
    }

    renderContent()
    {
        switch(this.state.activeTab)
        {
            case "About": return <About/>; break;
            case "Experience": return <Experience/>; break;
        }
    }

    render(){
        return (
            <div id="center-area">
                <Tabs setTab={this.SetTab}/>
                <div id="center-content">{this.renderContent()}</div>
            </div>
        );
    }
}

